.popup__content {
  width: calc(100% - 38px);
  max-width: 430px;
  min-width: 282px;
  position: fixed;
  left: 50%;
  top: 195px;
  transform: translateX(-50%);
  background-color: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  z-index: 2;
  padding: 34px 36px 37px 36px;
  box-sizing: border-box;
}

@media screen and (max-width: 468px) {
  .popup__content {
    top: 130px;
    left: 19px;
    transform: none;
    padding: 25px 22px 25px 22px;
  }
}
